"use client";
import React, { useState } from "react";
import api from "@/services/backend-api/authApi";
import { BaseInput } from "@/components/ui/Input/Input";
import FileUploader from "@/components/ui/FileUploader/FileUploader";
import { useRouter } from "next/navigation";

const ProfileForm: React.FC = () => {
  const [formData, setFormData] = useState({
    username: "",
    firstName: "",
    lastName: "",
  });
  const [avatar, setAvatar] = useState<File | null>(null);
  const router = useRouter()

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleFileUpload = (file: File) => {
    setAvatar(file);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const data = new FormData();
    data.append("username", formData.username);
    data.append("first_name", formData.firstName);
    data.append("last_name", formData.lastName);
    if (avatar) {
      data.append("avatar", avatar);
    }

    try {
      const response = await api.updateProfile(data);
      console.log("Profile updated:", response);
      router.push("/auth/profile/")
    } catch (error) {
      console.error("Failed to update profile:", error);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center max-w-70">
      <h2 className="text-2xl font-bold mb-5">Update Profile</h2>
      <form onSubmit={handleSubmit} className="flex flex-col gap-3">
        <BaseInput
          label="Username"
          id="username"
          name="username"
          value={formData.username}
          onChange={handleChange}
        />
        <BaseInput
          label="First name"
          id="firstName"
          name="firstName"
          value={formData.firstName}
          onChange={handleChange}
        />
        <BaseInput
          label="Last name"
          id="lastName"
          name="lastName"
          value={formData.lastName}
          onChange={handleChange}
        />
        <FileUploader onUpload={handleFileUpload}/>
          <button
            className="mt-5 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            type="button"
            onClick={handleSubmit}
          >
            Save
          </button>
      </form>
    </div>
  )
};

export default ProfileForm;
